import DdzBaseHandler from "./DdzBaseHandler";

export default class DdzAutoHandler extends DdzBaseHandler{
    protected execute(msg){
        let localSeat = this.SitHelper.serverSToLocalN(msg._src);
        let isAuto = msg._para.auto_play == 1;
        this.mainUI.callPlayer(localSeat, 'showAutoSign', isAuto);
        if(localSeat == 0){
            this.mainUI.askActionView.setAutoPlayBtnShow(!isAuto);
            this.mainUI.doResetPokers();
        }
    }

    protected executeSync(msg){
        this.execute(msg);
    }
}